<template>
  <div class="AliPay" v-if="isShow">
    <div class="cover"></div>
    <div class="tipBox">
      <p class="p-b">请在新的<span class="blue">支付宝支付</span>页面完成付款</p>
      <p class="p-s">请于{{lastTime}}前完成支付，否则订单将自动取消</p>
      <div class="btns">
        <div class="btn change" @click='handleClose()'>更换支付方式</div>
        <div class="btn payed" @click="handlePay">已支付</div>
      </div>
      <div class="close" @click="handleClose"><img src="../assets/images/close.png"></div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    isShow: {
      type: Boolean,
      default: false
    },
    lastTime: {
      type: Number
      // default: '2019-02-25 24:00:00'
    },
    orderSn: {
			default: ''
		}
  },
  methods: {
    handleClose () {
      this.$emit('toggle')
    },
		handlePay(){
			this.$api.product.getcheckPayment({
				sn: 2019030811716
				// sn: this.orderSn
			})
			.then(data => {
				console.log('data',data)
				if(data.code==0){
					this.$toast(data.message,'center');
					this.$emit('toggle');
				}else if(data.code==1){
					// this.$router.push(`/insuranceResult/${this.orderSn}`)
					this.$router.push(`/insuranceResult/2019030811716`)
				}
			})
		}
  },
  data () {
    return {}
  }
}
</script>

<style lang="scss" scoped>
  @import '@/assets/styles/mixin.scss';

  .AliPay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 19951105;
    .cover {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: #333333;
      opacity: .58;
      z-index: 1;
    }
    .tipBox {
      background: #FFFFFF;
      width: 400px;
      position: absolute;
      left: 50%;
      top: 50%;
      margin: -182px -220px;
      padding: 20px;
      z-index: 2;
      border-radius: 4px;
      .p-b {
        font-size: 16px;
        .blue {
          color: #29A1F7;
        }
      }
      .p-s {
        font-size: 12px;
        margin: -2px 0 18px;
      }
      .p-b, .p-s {
        color: #2A3449;
        line-height: 36px;
      }
      .btns {
        margin-top: 18px;
        height: 36px;
        text-align: right;
        .btn {
          display: inline-block;
          height: 34px;
          border-radius: 18px;
          border: 1px solid;
          font-size: 14px;
          line-height: 34px;
          text-align: center;
          margin-left: 16px;
          padding: 0 14px;
          cursor: pointer;
          &.change {
            border-color: #D5D8D8;
            color: #A5A8A8;
            background: #FFFFFF;
          }
          &.payed {
            border-color: $color-active-text;
            background: $color-active-text;
            color: #FFFFFF;
          }
        }
      }
      .close {
        position: absolute;
        right: 20px;
        top: 20px;
        width: 12px;
        height: 12px;
        img {
          display: block;
          width: 12px;
          height: 12px;
          cursor: pointer;
          z-index: 1;
        }
      }
    }
  }
</style>
